<template>
  <div id="app" v-cloak>
    <!---->
    <div id="img">
      <img src="./images/perMsg.png" alt="">
    </div>
    <!---->
    <div id="per">
      <p>{{name}} <span>{{age}}</span></p>
      <div>
        <div class="img" :style="{width:widthImg}">
          <div class="imgBoxs" v-for="item in imgs">
            <img :src="item" @click="imgSize(item,imgs)" alt="">
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div id="perData">
      <p></p>
      <div>
        <span>{{age}}</span>
        <span v-if="status==0"></span>
        <span v-if="status==1"></span>
        <span>{{dept}}</span>
        <span>{{sex}}</span>
      </div>
    </div>
    <div id="introduce">
      <p></p>
      <div>
        <p>{{remark==""?'':remark}}</p>
      </div>
    </div>
    <!---->
    <div id="newDynamic">
      <p></p>
      <div class="list" v-for="item in dynamicList">
        <p class="tent">{{item.dynamic.content}}</p>
        <div class="content">
          <div class="img" :style="{width:item.dynamic.widthList}">
            <div v-for="it in item.dynamic_img">
              <img :src="it" @click="imgSize(it,item.dynamic_img)" alt="">
            </div>
          </div>
        </div>
        <p class="reply">
          <span class="time">{{item.dynamic.createTime}}</span>
          <span class="replyMsg" @click="hf(item.dynamic.id)"><img style="vertical-align: middle"
                                                                   src="./images/rmsg.png"
                                                                   alt="">{{item.reply.length}}</span>
          <span v-if="item.type=='1'" class="fabulous" @click="Fabulous(item.dynamic.id)"><img
            src="./images/appreciate.png"
            alt="">{{item.dynamic.isGood}}</span>
          <span v-else class="fabulous" @click="Fabulous(item.dynamic.id)"><img src="./images/appreciate_01.png"
                                                                                alt="">{{item.dynamic.isGood}}</span>
        </p>
        <div class="otherMsg">
          <p v-for="ite in item.reply"><span>{{ite.replyName}}</span><span>{{ite.replyContent}}</span></p>
        </div>
      </div>
    </div>
    <!----->
    <div id="footer" v-if="id!=userid">
      <span class="follow" @click="follow" v-if="isfollow==0">+</span>
      <span class="follow" @click="followClick" v-if="isfollow==1"></span>
      <span class="letter" @click="letterMsg"></span>
    </div>
    <!---->
    <div id="bigImg" v-if="imgShow" @click="hideImg">
      <img :src="bigImage" alt="">
    </div>
    <!---->
    <div id="zz" v-show="show" @click="hide"></div>
    <div id="msg_model" v-show="show">
      <textarea placeholder="" v-model="replyContent"></textarea>
      <div :class="{'gary':msg_ison}" @click="msg_on"></div>
    </div>
    <!---->
    <div id="letterModel" v-show="showMsg" @click="hideMsg"></div>
    <div id="msg_letter" v-show="showMsg">
      <textarea placeholder="" v-model="msgLetter"></textarea>
      <div :class="{'gary':msg_ison}" @click="sendLetter"></div>
    </div>
  </div>


</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        imgList: [],
        imgMore: [],
        mine: '',
        userid: '',
        show: false,
        showMsg: false,
        imgs: [],
        lists: [],
        widthImg: '',
        widthList: '',
        id: '',
        name: '',
        age: '',
        dept: '',  //
        sex: '',
        remark: '',
        dynamicList: [],   //
        msg_ison: true,
        dynamicId: '',  //id
        replyContent: '',  //
        userName: '',
        msgLetter: '',  //
        imgShow: false,
        bigImage: '',
        status: '',
        isfollow: ""  //
      }

    },
    created: function () {
      $("#title").text("");
      this.$MintUI.Indicator.close();
      this.userid = this.WX_UserId;
      this.$MintUI.Indicator.open('');
    },
    mounted: function () {
      var that = this;
      that.id = that.$route.query.id;
      that.perMsg();  //
      that.getName();  //
      that.getJsApiConfig()
    },
    methods: {
      imgSize: function (e, list) {  //,
        var that = this;
        /* this.bigImage=e;
        this.imgShow=true; */
        wx.previewImage({
          current: e, // http
          urls: list // http
        });
      },
      hideImg: function () {
        this.bigImage = "";
        this.imgShow = false;
      },
      getJsApiConfig: function () {
        var that = this;
        var url = window.location.href;
        if (window.location.hash.length) {
          url = url.substr(0, url.indexOf(window.location.hash));
        }
        $.ajax({
          type: "post",
          url: that.port + "api/Service/GetJsApiConfig",
          data: {url: url},
          dataType: "json",
          success: function (res) {
            var result = JSON.parse(res.data);
            wx.config({
              beta: true,
              debug: false, // ,apialertpclogpc
              appId: that.Crop_Id, // cropID
              timestamp: result.timestamp, // 
              nonceStr: result.nonceStr, // 
              signature: result.signature,// 1
              jsApiList: ['openEnterpriseChat', "previewImage"] // JSJS2
            });


          }, error: function () {
          }
        });
      },
      letterMsg: function () {  //
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/IsQueQiao",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              if (res.data == "0") {   //
                that.$MintUI.Toast("");
              } else if (res.data == "1") {
                that.showMsg = true;
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
        /*   var that=this;
                               wx.ready(function () {
                                   wx.openEnterpriseChat({
                                           // userIdsexternalUserIdsuserIds+openIds2000
                                           userIds: that.id,    //userid1;userid2;...
                                           groupName: '',  // ""
                                           success: function(res) {
                                                   // 
                                                   console.log(res);
                                           },
                                           fail: function(res) {
                                                   console.log("res========"+res);
                                                   if(res.errMsg.indexOf('function not exist') > -1){
                                                           alert('')
                                                   }
                                           }
                                   });
                           }); */
      },
      hideMsg: function () {  //
        this.msgLetter = "";
        this.showMsg = false;
      },
      sendLetter: function () {  //
        var that = this;

        if (that.msgLetter == "") {
          that.$MintUI.Toast("");
          return false;
        }
        that.$MintUI.Indicator.open("");
        var title = "";
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/SendMessage",
          data: {sendId: that.userid, receiveId: that.id, title: title, content: that.msgLetter},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              that.msgLetter = "";
              that.showMsg = false;
              that.$MintUI.Toast(res.msg);
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast(res.msg);
            return false;
          }
        });


      },
      hf: function (e) {   //
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/IsQueQiao",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              if (res.data == "0") {   //
                that.$MintUI.Toast("");
              } else if (res.data == "1") {
                that.dynamicId = e;
                that.show = true;
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });

      },
      msg_on: function () {
        var that = this;
        if (that.replyContent == "") {
          that.$MintUI.Indicator.close();
          that.$MintUI.Toast("");
          return false;
        }
        that.$MintUI.Indicator.open("");
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/ReplyDynamic",
          data: {dynamicId: that.dynamicId, replyId: that.userid, replyContent: that.replyContent},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              var a = {
                replyName: that.userName,
                replyContent: that.replyContent
              }
              for (var i = 0; i < that.dynamicList.length; i++) {
                if (that.dynamicList[i].dynamic.id == that.dynamicId) {
                  that.dynamicList[i].reply.push(a);
                }
              }
              that.dynamicId = "";
              that.replyContent = "";
              that.show = false;
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });

      },
      hide: function () {
        this.show = false;
        this.dynamicId = "";
        this.replyContent = "";
      },
      Fabulous: function (e) {   //
        var that = this;
        that.$MintUI.Indicator.open("");
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/DynamicIsGood",
          data: {id: e, userid: that.userid},
          dataType: "json",
          success: function (res) {

            if (res.returncode == 0) {
              if (res.data == "1") {
                that.$MintUI.Indicator.close();
                that.$MintUI.Toast(res.msg);
                return false;
              }

              var data = JSON.parse(res.data);
              if (data.type == "1") {  //
                for (var i = 0; i < that.dynamicList.length; i++) {
                  if (that.dynamicList[i].dynamic.id == e) {
                    that.dynamicList[i].dynamic.isGood = data.is_good;
                    that.dynamicList[i].type = "0";
                  }
                }
              } else if (data.type == "0") {  //
                for (var i = 0; i < that.dynamicList.length; i++) {
                  if (that.dynamicList[i].dynamic.id == e) {
                    that.dynamicList[i].dynamic.isGood = data.is_good;
                    that.dynamicList[i].type = "1";
                  }
                }

              }
              that.$MintUI.Indicator.close();
            } else {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });

      },
      perMsg: function () {
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/GetQueQiaoUserInfo",
          data: {
            userid: that.id,
            loginid: that.userid
          },
          dataType: "json",
          success: function (res) {

            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              that.status = data.queqiao_user.status;
              that.name = data.queqiao_user.name;
              that.age = data.queqiao_user.age;
              that.widthImg = (data.user_img_list.length * 6.5) + "rem";
              for (var i = 0; i < data.user_img_list.length; i++) {  //
                data.user_img_list[i] = that.port + data.user_img_list[i];
              }
              that.imgs = data.user_img_list;
              that.dept = data.queqiao_user.dept;
              that.sex = data.queqiao_user.sex;
              that.remark = data.queqiao_user.remark;
              that.isfollow = data.queqiao_user.isfollow;
              for (var j = 0; j < data.dynamic.length; j++) {
                var width = (data.dynamic[j].dynamic_img.length * 5.3) + "rem";
                data.dynamic[j].dynamic['widthList'] = width;
                for (var k = 0; k < data.dynamic[j].dynamic_img.length; k++) {
                  data.dynamic[j].dynamic_img[k] = that.port + data.dynamic[j].dynamic_img[k];
                }

              }
              that.dynamicList = data.dynamic;


            }

          }, error: function () {
            that.$MintUI.Indicator.close();
          }
        });
      },
      getName: function () {   //name  
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/UserBaseInfo/GetUserBaseInfo",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              that.userName = data.name;
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },
      follow: function () {  //
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/AddFollow",
          data: {userid: that.id, followid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              if (res.data == "0") {  //
                that.isfollow = "1";
              }

              that.$MintUI.Toast(res.msg);
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });

      }, followClick: function () {
        var that = this;
        var userid = userid;
        var followid = followid;
        $.ajax({
          type: 'post',
          url: that.port + 'api/QueQiao/DelFollow',
          data: {userid: that.id, followid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == "0") {
              that.$MintUI.Toast(res.msg);
              if (res.data == "0") {  //
                that.isfollow = "0";
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Toast("");

          }
        })
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "./css/detailed.css"
  /*# sourceMappingURL=home.css.map */
</style>



