<template>
  <div id="Linformation">
    <div id="msg">
      <!-- -->
      <div class="header">
        <span></span>
        <img class="right" src="./images/right.png" alt="">
        <img class="head" :src="img" alt="" id="view">
        <input type="file" id="file" accept="image/jpeg,image/png,image/jpg">
      </div>
      <!---->
      <!-- 	<div class="name">
                 <span></span><span class="peoName">{{name}}</span>
             </div> -->
      <!---->
      <div class="name">
        <span></span>
        <span class="peoName" v-if="gender==1"></span>
        <span class="peoName" v-if="gender==2"></span>
      </div>
      <!---->
      <div class="sex">
        <span></span>
        <img src="./images/right.png" alt="">
        <input type="text" id="picker" placeholder="">
        <!--<select name="" v-model="marry">
          <option value="0"></option>
          <option value="1"></option>
        </select>-->
      </div>
      <!---->
      <div class="age">
        <span></span>
        <input type="text" placeholder="" v-model="place">
      </div>
      <!---->
      <!-- <div class="name">
                 <span></span>
                 <span class="peoName">{{phone}}</span>
             </div> -->
    </div>
    <div id="real">
      <!---->
      <div class="age" style="display: flex;">
        <span  class="mustFill" style="flex: none"></span>
        <input style="width: 90%;" type="tel" placeholder="" v-model="idCard">
      </div>
      <!---->
      <div class="age"  style="display: flex;">
        <span style="flex:none;"></span>
        <!-- <img style="float: right;width: 1.2rem;height: 1.2rem;margin-right: 0.3rem;margin-top: 0.575rem;" src="./images/right.png" alt=""> -->
        <!-- <input id="demo1" type="text" readonly="" name="input_date" :value="age" placeholder="" data-lcalendar="1900-01-01,2100-12-31"  unselectable="on" onfocus="this.blur()"/> -->
        <input style="width: 90%;" type="tel" readonly="" name="input_date" :value="age" placeholder="18"
               data-lcalendar="1900-01-01,2100-12-31" unselectable="on" onfocus="this.blur()"/>
      </div>
      <!---->
      <div class="sex">
        <span></span>
        <img src="./images/right.png" alt="">
        <input type="text" id="education" placeholder="">
        <!--<select name="" v-model="education">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>-->
      </div>
      <!-- -->
      <div class="name">
        <span></span>
        <span class="peoName">{{phone}}</span>
      </div>

    </div>

    <!---->
    <div id="mine">
      <!---->
      <div class="brief">
        <span style="width:24%;"></span><input type="text" placeholder="" v-model="mine">
      </div>
      <!---->
      <div class="upload">
        <p class="mustFill"></p>
        <div>
          <div class="list" v-for="(item,index) in imgList">
            <img class="imag" @click="imgSize(url1+item,imgList)" :src="url1+item">
            <img class="del" @click="del(index)" src="./images/close.png" alt="">
          </div>

          <div v-show="imgList.length<9" class="album" @click="xmTanUploadImg1">
            <!-- <input id="upImgList" type="file" accept="image/*" @change="upImgList"> -->
            <img src="./images/addImg.png" alt="">
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <div id="clipArea" v-show="coreImg"></div>
    <span id="clipBtn" v-show="coreImg"></span>


    <!---->
    <div id="bigImg" v-if="show" @click="hideImg">
      <img :src="bigImage" alt="">
    </div>

    <!-- -->
    <div id="model" v-show="IsAddS"></div>
    <div id="isAdd" v-show="IsAddS">
      <P>
        <img src="./images/ts.png" alt="">
        <span></span>
      </P>
      <div class="content">
        
      </div>
      <div class="bottom">
        <span class="no" @click="nIsAddS"></span>
        <span class="yes" @click="yIsAddS"></span>
      </div>

    </div>
    <!---->
    <div id="submit" @click="submit">
      
    </div>
  </div>
</template>

<script>

  export default {
    name: 'HelloWorld',
    data() {
      return {
        img: require('./images/mrHeader.jpg'),  //
        sex: '',   //
        marry: '0',  //
        phone: '',  //
        age: '',   //
        place: '',  //
        idCard: '',  //
        imgList: [],   //
        imgMore: [],   //
        mine: '',  //
        faceImg: '',  //-
        userid: '',
        name: '',
        gender: '',
        url1: '',
        show: false,
        bigImage: '',
        coreImg: false,
        education: '',   //
        IsAddS: false,
        yearM: "",
        type: ""  //  ios   1 2ios

      }

    },
    created: function () {
      $("#title").text("");
      this.$MintUI.Indicator.close();
      clearTimeout(this.setTime);
      this.userid = this.WX_UserId;
      this.url1 = this.url;
      this.$nextTick(function () {
        $(".weui-picker-container").hide();
      });
      // this.$MintUI.Indicator.open("");
    },
    mounted: function () {
      this.getTime();
      /* var calendar = new lCalendar();
                 calendar.init({
                     'trigger': '#demo1',
                     'type': 'date'
                 }); */
      this.getSignature();//
      this.getMsg();//
      var fileName;
      var that = this;
      var clipArea = new bjj.PhotoClip("#clipArea", {
        size: [],
        outputSize: [],
        file: "#file",
        view: "#view",
        ok: "#clipBtn",
        loadStart: function (e) {
          that.coreImg = true;
          fileName = e.name;
        },
        loadComplete: function (e) {
          //console.log("");
        },
        clipFinish: function (dataURL) {
          that.coreImg = false;
          that.$MintUI.Indicator.open("");
          $.ajax({
            url: that.port + "api/FeedBack/UpImage",
            type: "POST",
            data: {
              base64_string: dataURL,
              fileExt: fileName,
              gao: '100',
              kuan: '100'
            },
            dataType: "json",
            success: function (res) {
              if (res.returncode == 0) {
                that.faceImg = res.data;
                that.img = that.url1 + res.data;
                that.$MintUI.Indicator.close();
              } else {

                that.$MintUI.Indicator.close();
                that.$MintUI.Toast(res.msg);
              }
            }, error: function () {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast("");

            }
          });

        }
      });

      $("#picker").picker({
        title: "",
        cols: [
          {
            textAlign: 'center',
            values: ['', '']
          }
        ]
      });

      $('#education').picker({
        title: '',
        cols: [
          {
            textAlign: 'center',
            values: ['', '', '', '', '', '', ''],
          }
        ]
      });
    },
    methods: {
      getSignature: function () {   //
        var that = this;
        var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        if (isAndroid) {
          that.type = "1";
        } else if (isiOS) {
          that.type = "2";
        }
        var url = window.location.href;
        if (window.location.hash.length) {
          url = url.substr(0, url.indexOf(window.location.hash));
        }
        $.ajax({
          type: "post",
          url: that.port + "api/Service/GetJsApiConfig",
          data: {url: url},
          dataType: "json",
          success: function (res) {
            var result = JSON.parse(res.data);
            wx.config({
              debug: false, // ,apialertpclogpc
              appId: that.Crop_Id + '', // cropID
              timestamp: result.timestamp, // 
              nonceStr: result.nonceStr + '', // 
              signature: result.signature + '',// 1
              jsApiList: ['chooseImage', 'uploadImage', 'getLocalImgData', 'previewImage'] // JSJS2
            });

          }, error: function () {
            that.wxError();
          }
        });
      },
      iosUpImg: function (e) {   //ios,,
        var that = this;
        wx.getLocalImgData({
          localId: e, // localID
          success: function (res) {
            var localData = res.localData; // localDatabase64img
            var fileName = "1.jpg";
            $.ajax({
              url: that.port + "api/FeedBack/UpImage",
              type: "POST",
              data: {
                base64_string: localData,
                fileExt: fileName,
              },
              dataType: "json",
              success: function (res) {
                if (res.returncode == 0) {
                  that.imgList.push(res.data);
                } else {
                  that.$MintUI.Toast("");
                }
              }, error: function () {
                that.$MintUI.Toast("");
              }
            });
          }
        });
      },
      androidUpImg: function (e) {   //  ,,,
        var that = this;
        wx.uploadImage({
          localId: e, // IDchooseImage
          isShowProgressTips: 1, // 1
          success: function (res) {
            var media_id = res.serverId; // ID
            //  
            $.ajax({
              url: that.port + "api/Service/GetMedia",
              data: {
                media_id: media_id
              },
              type: "post",
              dataType: "json",
              success: function (res) {
                if (res.returncode == 0) {
                  that.imgList.push(res.data);
                } else {
                  that.$MintUI.Toast("");
                }
              },
              error: function (XMLHttpRequest, textStatus, errorThrown) {
                that.$MintUI.Toast("");
              }
            });
          }
        });
      },
      //
      xmTanUploadImg1: function (e) {
        var that = this;
        wx.ready(function () {
          wx.chooseImage({
            count: 9, // 9
            sizeType: ['original', 'compressed'], // 
            sourceType: ['album', 'camera'], // 
            defaultCameraMode: "batch", //normalbatchnormalbatchnormal
            success: function (res) {
              if (that.type == "1") {
                for (var i = 0; i < res.localIds.length; i++) {
                  var localIds = res.localIds[i].toString();
                  that.androidUpImg(localIds);
                }
              } else {
                for (var i = 0; i < res.localIds.length; i++) {
                  var localIds1 = res.localIds[i].toString();
                  that.iosUpImg(localIds1);
                }
              }
              // andriodlocalIdimgsrc
              // IOSgetLocalImgDatabase64img

            }
          });
        });
      },
      getTime: function () {  //
        var that = this;
        var date = new Date;
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var data = date.getDate();
        that.year = year;
        that.month = month;
        if (that.month.toString().length == 1) {
          that.month = "0" + that.month;
        }
        that.yearM = that.year + "-" + that.month + "-" + date.getDate();
      },
      yIsAddS: function () {  //
        var that = this;
        that.$MintUI.Indicator.open("");
        var img = that.imgList.toString();
        var marry = '';
        if ($('#picker').val() == '') {
          marry = '0'
        } else if ($('#picker').val() == '') {
          marry = '1'
        }
        var education = $('#education').val();
        var mine = encodeURI(that.mine);
        $.ajax({
          url: that.port + "api/QueQiao/UpdateQueQiaoUser",
          type: "POST",
          data: {
            userid: that.userid,
            status: marry,
            remark: mine,
            img: img,
            faceImg: that.faceImg,
            place: that.place,
            birthday: that.age,
            education: education,
            card: that.idCard,
            contact: that.phone,
          },
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              that.$MintUI.Indicator.close();
              that.IsAddS = false;
              that.$MintUI.Toast("");
              setTimeout(function () {
                that.$router.push('/love/LperCenter');
              }, 1500);
            } else {
              that.$MintUI.Toast("");
              that.$MintUI.Indicator.close();
            }
          }, error: function () {
            that.$MintUI.Toast("");
            that.$MintUI.Indicator.close();
          }
        });
      },
      nIsAddS: function () {
        this.IsAddS = false;
      },
      imgSize: function (e, list) {  //
        /* 						this.bigImage=e;
                                    this.show=true; */
        var that = this;
        var arr = [];
        for (var i = 0; i < list.length; i++) {
          arr.push(that.url1 + list[i]);
        }
        wx.ready(function () {
          wx.previewImage({
            current: e, // http
            urls: arr // http
          });
        });
      },
      hideImg: function () {
        this.bigImage = "";
        this.show = false;
      },
      getMsg: function () {   //name  
        var that = this;
        this.setTime = setTimeout(function () {
          that.$MintUI.Indicator.open("");
        }, that.timeNum);
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/QueUserBaseInfo",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              that.name = data.userinfo.name;
              that.phone = data.userinfo.mobile;
              that.gender = data.userinfo.gender;
              if (data.queqiao == null || data.queqiao == "") {
              } else {
                if (data.queqiao.status == 0) {
                  $('#picker').val('');
                } else if (data.queqiao.status == 1) {
                  $('#picker').val('');
                }

                that.place = data.queqiao.place;
                if (data.queqiao.education != null) {
                  $('#education').val(data.queqiao.education)
                }
                if(data.queqiao.card != null && data.queqiao.card !=''){
                  that.idCard = data.queqiao.card;
                }else{
                  that.idCard= '';
                }
                if (data.queqiao.birthday != null) {
                  that.age = data.queqiao.birthday.split(" ")[0];
                }
                if(data.queqiao.remark != null &&data.queqiao.remark !=''){
                  that.mine = decodeURI(data.queqiao.remark);
                }else{
                  that.mine = ''

                }
                if (data.queqiao.faceImg == null || data.queqiao.faceImg == "") {

                } else {
                  that.faceImg = data.queqiao.faceImg;
                  that.img = that.port + data.queqiao.faceImg;
                }
              }
              if (data.imglist == null || data.imglist == "") {
              } else {
                that.imgList = data.imglist;
              }
              that.$MintUI.Indicator.close();
            } else {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast("");
              return false;
            }

          }, error: function () {
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },
      xmTanUploadImg: function (e) {  //
        var obj = e.target;
        that.$MintUI.Indicator.open("");
        var that = this;
        var file = obj.files[0];
        var fileName = file.name;
        var fileType = file.type.slice(file.type.indexOf('/') + 1).toLowerCase();
        if (fileType == 'jpg' || fileType == 'png' || fileType == 'jpeg' || fileType == 'JPG' || fileType == 'PNG' || fileType == 'JPEG') {
          lrz(file, {width: 450})
            .then(function (rst) {
              $.ajax({
                url: that.port + "api/FeedBack/UpImage",
                type: "POST",
                data: {
                  base64_string: rst.base64,
                  fileExt: fileName,
                  gao: '100',
                  kuan: '100'
                },
                dataType: "json",
                success: function (res) {
                  if (res.returncode == 0) {
                    that.faceImg = res.data;
                    that.img = port + res.data;
                    that.$MintUI.Indicator.close();
                  } else {
                    that.$MintUI.Indicator.close();
                  }
                }, error: function () {
                  that.$MintUI.Indicator.close();
                  that.$MintUI.Toast("");
                }
              });
            }).catch(function (err) {
          }).always(function () {
            obj.value = null;
            that.$MintUI.Indicator.close();
          });
        } else {
          that.$MintUI.Indicator.close();
          that.$MintUI.Toast("");
          return false;
        }
      },
      del: function (e) {  //
        this.imgList.splice(e, 1);
      },
      submit: function () {   //
        var that = this;
        if (that.idCard != null && that.idCard.length != 18  &&!(/^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/.test(that.idCard))) {
          that.$MintUI.Toast("18");
          that.IsAddS = false;
          return false;
        }
        if (that.age != "") {
          if (Number(that.age.split("-")[0]) > Number(that.yearM.split("-")[0])) {
            that.$MintUI.Toast("");
            that.IsAddS = false;
            return false;
          }
          if (Number(that.age.split("-")[0]) == Number(that.yearM.split("-")[0])) {
            if (Number(that.age.split("-")[1]) > Number(that.yearM.split("-")[1])) {
              that.$MintUI.Toast("");
              that.IsAddS = false;
              return false;
            }
            if (Number(that.age.split("-")[1]) == Number(that.yearM.split("-")[1])) {
              if (Number(that.age.split("-")[2]) > Number(that.yearM.split("-")[2])) {
                that.$MintUI.Toast("");
                that.IsAddS = false;
                return false;
              }
            }
          }
        }
        if (that.imgList.length <= 0) {
          that.$MintUI.Toast("");
          return false;
        }
        that.IsAddS = true;
      }
    },
    watch: {
      idCard: function (value) {
        var that = this;
        if (value != "" && value != null) {
          if (value.length == 18) {
            that.age = value.substr(6, 4) + "-" + value.substr(10, 2) + "-" + value.substr(12, 2)
          } else {
            that.age = "";
          }
        }
      }
    }
  }


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "./css/information.css";

  #Linformation {
    width: 100%;
    height: 100%;
    overflow: auto;
  }

  #clipArea {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
  }

  #clipBtn {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 99;
    color: #0094ff;
  }

  .mustFill:after{
    content:'*';
    color:red;
    margin-left: .2rem;
  }

  /*# sourceMappingURL=home.css.map */
</style>



