<template>
  <div class="detail" v-infinite-scroll="perMsg" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
    <!---->
    <div id="img" class="swiper-container" v-show="imgs.length>0">
      <!--<div class="paging">
        {{nowPage}}/{{imgs.length}}
      </div>-->
      <div class="swiper-wrapper">
        <div class="swiper-slide" style="overflow: hidden;" v-for="item in imgs">
          <img ref="allImgs" :src="item" @click="imgSize(item,imgs)">
        </div>
      </div>
    </div>
    <div v-show="imgs.length<=0" id="img">
      <img v-changeSize src="./images/theBG.jpeg">
    </div>
    <!---->
    <div id="per">
      <!--()-->
      <div class="circle" style="color:#f98b8b;border:1px solid #f98b8b;" v-if="id!=userid" @click="follow"
           v-show="isfollow==0"><img src="./images/jy-21+.png"><span></span>
      </div>

      <!--()-->
      <div class="circle" style="color:#fff;border:1px solid #f98b8b;background: #f98b8b;" v-if="id!=userid"
           @click="followClick" v-show="isfollow==1"><img src="./images/jy-21.png"><span></span>
      </div>

      <!---->
      <div v-if="id!=userid" class="circle" style="right: 5.25rem;" @click="letterMsg()"><img src="./images/jy-22+.png"
                                                                                              alt=""><span>TA</span>
      </div>
      <p>{{name}}
        <img v-if="theSex == ''" src="./images/jy-4.png">
        <img v-if="theSex == ''" src="./images/jy-5.png">
      </p>
      <div class="work">{{age}}/{{dept}}</div>
      <div class="description" v-html="remark!=''&&remark!=null?decodeURI(remark):'TA'"></div>
    </div>

    <!---->
    <!--<div id="perData">
      <p></p>
      <div>
        <span>{{age}}</span>
        <span v-if="status==0"></span>
        <span v-if="status==1"></span>
        <span>{{dept}}</span>
        <span>{{sex}}</span>
      </div>
    </div>
    <div id="introduce">
      <p></p>
      <div>
        <p>{{remark==""?'':remark}}</p>
      </div>
    </div>-->

    <!---->
    <div id="newDynamic">
      <p></p>
      <div class="list"
           v-for="(item,index) in dynamicList">
        <div class="clock">
          <div class="theTime">
            <span class="days">{{item.dynamic.createTime.split(' ')[0].split('-')[2]}}</span><span>/</span><span
            class="months">{{item.dynamic.createTime.split(' ')[0].split('-')[1]}}</span>
          </div>
          <div class="prise">
             <span v-if="item.type ==1" class="fabulous" @click="Fabulous(item.dynamic.id,index)">
              <img src="./images/jy-26.png" alt="">
               <!--{{item.dynamic.isGood}}-->
               
            </span>
            <span style="background: #f98b8b;color:#fff;border:0" v-if="item.type ==0" class="fabulous"
                  @click="Fabulous(item.dynamic.id,index)">
              <img src="./images/jy-20.png" alt="">
              <!--{{item.dynamic.isGood}}-->
               
            </span>
            <span style="float: right;" class="fabulous" @click="hf(item.dynamic.id,index)">
              <img src="./images/jy-27.png" alt="">
                
              <!--{{item.reply.length}}-->
            </span>
          </div>
        </div>
        <p class="tent">{{decodeURI(item.dynamic.content)}}</p>
        <div class="content">
          <div class="img"><!-- :style="{width:item.dynamic.widthList}"-->
            <div v-for="it in item.dynamic_img" v-if="item.dynamic_img.length>0">
              <img v-changeSize :src="url+it" @click="imgSize1(url+it,item.dynamic_img)" alt="">
            </div>
          </div>
        </div>
        <div class="reply">
          <!--<span class="time">{{item.dynamic.createTime}}</span>-->
          <div class="msgHead" v-if="item.isgoodname != '' && item.isgoodname != null">
            <span class="replyMsg">
            <img style="margin-right: .75rem;" src="./images/jy-23.png" alt="">
              <span class="praiseName">
              {{item.isgoodname}}
              </span>
            </span>
          </div>
          <div class="otherMsg" v-if="item.reply.length>0">
            <p v-for="(ite,indx) in item.reply" @click.stop="delMsg(index,indx,ite.id,ite.createBy)">
              <span class="replyName">{{ite.replyName}}</span>
              <span class="replyCont">
                {{decodeURI(ite.replyContent)}}
              </span>
              <!--<span @click="delMsg(ite.id)" v-if="ite.replyId == WX_UserId" class="delMsg">
                <img src="./images/delMsg.png" alt="">
              </span>-->
            </p>
          </div>
        </div>
      </div>
      <!---->
      <div v-show="loadingShow"
           style="overflow:hidden;padding:0.5rem 0;margin:0 auto;color:#aaa;width:4.5rem;font-size:0.75rem;text-align:center;">
        <mt-spinner style="float:left;" :type="3" color="#aaa" :size="20"></mt-spinner>
        <span style="font-size: 12px;"></span>
      </div>

      <div v-if="noDynamic"
           style="font-size: 12px;padding: .5rem;color: #aaa;background: #f5f5f5;text-align: center;overflow: hidden;">------------
      </div>
      <div v-show="noData" style="font-size: 12px;padding: .5rem;color: #aaa;text-align: center;clear: both">------------
      </div>

    </div>
    <!----->
    <!--<div id="footer" v-if="id!=userid">
      <span class="follow" @click="follow" v-if="isfollow==0">+</span>
      <span class="follow" @click="followClick" v-if="isfollow==1"></span>
      <span class="letter" @click="letterMsg"></span>
    </div>-->
    <!---->
    <div id="bigImg" v-if="imgShow" @click="hideImg">
      <img :src="bigImage" alt="">
    </div>
    <!---->
    <!--<div id="zz" v-show="show" @click="hide"></div>
    <div id="msg_model" v-show="show">
      <textarea placeholder="" v-model="replyContent"></textarea>
      <div :class="{'gary':msg_ison}" @click="msg_on"></div>
    </div>-->
    <!---->
    <div id="letterModel" v-show="showMsg" @click="hideMsg">
      <div id="msg_letter" @click.stop>
        <h4><img src="./images/jy-22+.png">{{name}}</h4>
        <textarea placeholder="" v-model="msgletter"></textarea>
        <div>
          <span class="yesSend" :class="{'gary':msg_ison}" @click="sendletter"></span>
          <span class="noSend" @click="hideMsg"></span>
        </div>
      </div>
    </div>
    <div class="footer" v-show="show" @click="hide">
      <div class="total" @click.stop>

        <div class="insert">
          <img src="./images/cy-fb3.png" alt="">
          <input ref="response" type="text" placeholder="..." v-model="replyContent">
        </div>
        <div class="publish" @click="msg_on"></div>
      </div>
    </div>
  </div>


</template>

<script>
  import Swiper from 'swiper'
  import {MessageBox} from 'mint-ui';

  export default {
    name: 'HelloWorld',
    data() {
      return {
        noData: false, //
        imgList: [],
        imgMore: [],
        mine: '',
        userid: '',
        show: false,
        showMsg: false,
        imgs: [],
        lists: [],
        widthImg: '',
        widthList: '',
        id: '',
        name: '',
        age: '',
        dept: '',  //
        sex: '',
        remark: '',
        dynamicList: [],   //
        msg_ison: true,
        dynamicId: '',  //id
        replyContent: '',  //
        userName: '',
        msgletter: '',  //
        imgShow: false,
        bigImage: '',
        status: '',
        isfollow: "",  //
        noDynamic: false,
        pageIndex: 0,
        pageSize: 4,
        loading: true,
        nowPage: 1,
        pushMsg:0,
        loadingShow:false,
        theSex:0,
      }

    },
    //
    directives: {
      changeSize: {
        inserted: function (el) {
          var getImgInfo = function (url) {
            return new Promise(resovle => {
              var img = new Image();
              var json = {};
              img.src = url;
              img.onload = function () {
                json['width'] = img.width;
                json['height'] = img.height;
                resovle(json);
              }
            })
          };
          var maxwidth = el.parentNode.offsetWidth;
          getImgInfo(el.src).then(res => {
            var that = this;
            var theWidth = el.width;
            var theHeight = el.height;
            if (theWidth > theHeight) {
              el.style.height = '100%';
              el.style.width = 'auto';
            } else {
              el.style.height = 'auto';
              el.style.width = '100%';
            }
          })
        }
      }
    },

    created: function () {
      $("#title").text("");
      this.$MintUI.Indicator.close();
      this.$nextTick(function () {
        $(".weui-picker-container").hide();
      });
      this.userid = this.WX_UserId;
      // this.$MintUI.Indicator.open('');
    },
    mounted: function () {
      var that = this;
      that.id = that.$route.query.id;
      that.perMsg();  //
      that.getName();  //
      that.getJsApiConfig();
      var mySwiper = new Swiper('.swiper-container', {
        autoplay: true,//
        loop: true,
        delay: 2000,
        observer: true,//swiperswiper
        observeParents: true,//swiperswiper
        type : 'fraction',
        on: {
          touchMove: function (event) {
            that.nowPage = mySwiper.activeIndex + 1;
          },
        },
      })
    },
    methods: {
      showBig:function(){

      },

      //
      delMsg: function (index,indx,theId, theUserid) {
        if(theUserid == this.userid){
          var that = this;
          MessageBox.confirm('?').then(action => {
            // alert(theId);
            $.ajax({
              type: "post",
              url: that.port + "api/QueQiao/DelDynamicReply",
              data: {
                id: theId,
                userid: that.userid,
              },
              dataType: "json",
              success: function (res) {
                if (res.returncode == 0) {
                  that.dynamicList[index].reply.splice(indx,1);
                }
              }
            })
          });
        }
      },

      imgSize: function (e, list) {  //,
        var that = this;
        /* this.bigImage=e;
        this.imgShow=true; */
        console.log(e);
        console.log(list);
        wx.previewImage({
          current: e, // http
          urls: list // http
        });
      },
      imgSize1: function (e, list) {  //,
        var that = this;
        var ss = [];
        for(var i = 0;i<list.length;i++){
          var theImg = that.url+list[i]
          ss.push(theImg)
        }
        console.log(e);
        console.log(ss);
        wx.previewImage({
          current: e, // http
          urls: ss // http
        });
      },

      hideImg: function () {
        this.bigImage = "";
        this.imgShow = false;
      },

      getJsApiConfig: function () {
        var that = this;
        var url = window.location.href;
        if (window.location.hash.length) {
          url = url.substr(0, url.indexOf(window.location.hash));
        }
        $.ajax({
          type: "post",
          url: that.port + "api/Service/GetJsApiConfig",
          data: {url: url},
          dataType: "json",
          success: function (res) {
            var result = JSON.parse(res.data);
            wx.config({
              beta: true,
              debug: false, // ,apialertpclogpc
              appId: that.Crop_Id, // cropID
              timestamp: result.timestamp, // 
              nonceStr: result.nonceStr, // 
              signature: result.signature,// 1
              jsApiList: ['openEnterpriseChat', "previewImage"] // JSJS2
            });
          }, error: function () {
            that.wxError();
          }
        });
      },
      letterMsg: function () {  //
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/IsQueQiao",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              if (res.data == "0") {   //
                that.$MintUI.Toast("");
                setTimeout(function () {
                  that.$router.push({path: '/love/lPerCenter'});
                }, 1000);
              } else if (res.data == "1") {
                that.showMsg = true;
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
        /*  var that = this;
          wx.ready(function () {
            wx.openEnterpriseChat({
              // userIdsexternalUserIdsuserIds+openIds2000
              userIds: that.id,    //userid1;userid2;...
              groupName: '',  // ""
              success: function (res) {
                // 
                console.log(res);
              },
              fail: function (res) {
                console.log("res========" + res);
                if (res.errMsg.indexOf('function not exist') > -1) {
                  alert('')
                }
              }
            });
          });*/
      },
      hideMsg: function () {  //
        this.msgletter = "";
        this.showMsg = false;
      },

      //
      sendletter: function () {
        var that = this;
        if (that.msgletter == "") {
          that.$MintUI.Toast("");
          return false;
        }
        that.$MintUI.Indicator.open("");
        var msgletter = encodeURI(that.msgletter);
        var title = "";
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/SendMessage",
          data: {sendId: that.userid, receiveId: that.id, title: title, content: msgletter},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              that.msgletter = "";
              that.showMsg = false;
              that.$MintUI.Toast(res.msg);
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast(res.msg);
            return false;
          }
        });
      },

      hf: function (e,index) {   //
        var that = this;
        that.pushMsg= index;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/IsQueQiao",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              if (res.data == "0") {   //
                that.$MintUI.Toast("");
              } else if (res.data == "1") {
                $('.detail').css('padding-bottom', '3rem');
                that.$refs.response.focus();
                that.dynamicId = e;
                that.show = true;
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },

      msg_on: function () {
        var that = this;
        if (that.replyContent == "") {
          that.$MintUI.Indicator.close();
          that.$MintUI.Toast("");
          return false;
        }
        $('.detail').css('padding-bottom', 0);
        // that.$MintUI.Indicator.open("");
        var replyContent = encodeURI(that.replyContent)
        debugger
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/ReplyDynamic",
          data: {dynamicId: that.dynamicId, replyId: that.userid, replyContent: replyContent},
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            var ss = JSON.parse(res.data).retmodel;
            if (res.returncode == 0) {
              var a = {
                replyName: that.userName,
                replyContent: decodeURI(that.replyContent)
              }
              for (var i = 0; i < that.dynamicList.length; i++) {
                if (that.dynamicList[i].reply.id == that.dynamicId) {
                  that.dynamicList[i].reply.push(a);
                }
              }

              that.dynamicList[that.pushMsg].reply.push(ss);

              if (that.dynamicList.length <= 0) {
                that.noDynamic = true;
              } else {
                that.noDynamic = false;
              }
              that.dynamicId = "";
              that.replyContent = "";
              that.show = false;
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },

      hide: function () {
        $('.detail').css('padding-bottom', 0);
        this.show = false;
        this.dynamicId = "";
        this.replyContent = "";
      },

      Fabulous: function (e, index) {   //
        var that = this;
        // that.$MintUI.Indicator.open("");
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/DynamicIsGood",
          data: {id: e, userid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              if (data.type == "1") {  //
                /*  for (var i = 0; i < that.dynamicList.length; i++) {
                    if (that.dynamicList[i].dynamic.id == e) {
                      that.dynamicList[i].dynamic.isGood = data.is_good;
                      that.dynamicList[i].type = "0";
                    }
                  }*/
                that.dynamicList[index].dynamic.isGood = data.is_good;
                that.dynamicList[index].type = "0";
                that.dynamicList[index].isgoodname = data.names;

              } else if (data.type == "0") {  //
                /* for (var i = 0; i < that.dynamicList.length; i++) {
                   if (that.dynamicList[i].dynamic.id == e) {
                     that.dynamicList[i].dynamic.isGood = data.is_good;
                     that.dynamicList[i].type = "1";
                   }
                 }*/
                that.dynamicList[index].dynamic.isGood = data.is_good;
                that.dynamicList[index].type = "1";
                that.dynamicList[index].isgoodname = data.names;
              }
              that.$MintUI.Indicator.close();
            } else {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },

      perMsg: function () {
        var that = this;
        this.loading = true;
        if (that.pageIndex > 0) {
          that.loadingShow = true;
        }
        this.setTime = setTimeout(function () {
          that.$MintUI.Indicator.open("");
        }, that.timeNum);
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/GetQueQiaoUserInfo",
          data: {
            userid: that.id,
            loginid: that.userid,
            pageIndex: that.pageIndex,
            pageSize: that.pageSize,
          },
          dataType: "json",
          success: function (res) {
            that.loadingShow = false;
            that.$MintUI.Indicator.close();
            clearTimeout(that.setTime);
            if (res.returncode == 0) {

              debugger
              console.log(that.pageIndex);
              var data = JSON.parse(res.data);
              that.status = data.queqiao_user.status;
              that.name = data.queqiao_user.name;
              that.age = data.queqiao_user.age;
              that.theSex = data.queqiao_user.sex;
              that.widthImg = (data.user_img_list.length * 6.5) + "rem";
              for (var i = 0; i < data.user_img_list.length; i++) {  //
                data.user_img_list[i] = that.port + data.user_img_list[i];
              }
              that.imgs = data.user_img_list;
              console.log(that.imgs);
              //
              that.dept = data.queqiao_user.dept;
              that.sex = data.queqiao_user.sex;
              that.remark = data.queqiao_user.remark;
              that.isfollow = data.queqiao_user.isfollow;
              var theData = data.dynamic;
              for (var j = 0; j < data.dynamic.length; j++) {
                // var width = (data.dynamic[j].dynamic_img.length * 5.3) + "rem";
                // data.dynamic[j].dynamic['widthList'] = width;
                // for (var k = 0; k < data.dynamic[j].dynamic_img.length; k++) {
                //   data.dynamic[j].dynamic_img[k] = that.port + data.dynamic[j].dynamic_img[k];
                // }
                that.dynamicList.push(data.dynamic[j]);
              }
              console.log(that.dynamicList);

              if (theData.length < that.pageSize) {
                that.loading = true;
                if (that.pageIndex <= 0 && that.dynamicList.length <= 0) {
                  that.noDynamic = true;
                } else {
                  that.noData = true;
                }
              } else {
                that.noData = false;
                that.noDynamic = false;
                that.$nextTick(function () {
                  that.loading = false;
                  that.pageIndex++;
                })
              }
            }
          }, error: function () {
            that.loadingShow = false;
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
          }
        });
      },
      getName: function () {   //name  
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/UserBaseInfo/GetUserBaseInfo",
          data: {userid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              that.userName = data.name;
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },

      follow: function () {  //
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/AddFollow",
          data: {userid: that.id, followid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == 0) {
              if (res.data == "0") {  //
                that.isfollow = "1";
              }
              // that.$MintUI.Toast(res.msg);
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },
      followClick: function () {
        var that = this;
        var userid = userid;
        var followid = followid;
        $.ajax({
          type: 'post',
          url: that.port + 'api/QueQiao/DelFollow',
          data: {userid: that.id, followid: that.userid},
          dataType: "json",
          success: function (res) {
            if (res.returncode == "0") {
              // that.$MintUI.Toast(res.msg);
              if (res.data == "0") {  //
                that.isfollow = "0";
              }
            } else {
              that.$MintUI.Toast(res.msg);
              return false;
            }
          }, error: function () {
            that.$MintUI.Toast("");

          }
        })
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "./css/detail.css";


  /*# sourceMappingURL=home.css.map */
</style>



