<template>
  <div id="dynamics">
    <!---->
    <div id="mine">
      <!---->
      <div class="brief">
        <textarea name="" v-model="mine" placeholder=""></textarea>
      </div>
      <!---->
      <div class="upload">
        <div>
          <div class="list" v-for="(item,index) in imgList">
            <img class="imag" @click="imgSize(item,imgList)" :src="item">
            <img class="del" @click="del(index)" src="./images/close.png" alt="">
          </div>
          <div v-show="imgList.length<9" class="album" @click="xmTanUploadImg">
            <!-- <input type="file" accept="image/*" @change="upImgList"> -->
            <img src="./images/addImg.png" alt="">
          </div>

        </div>
      </div>
    </div>

    <!---->
    <div id="bigImg" v-if="show" @click="hideImg">
      <img :src="bigImage" alt="">
    </div>

    <!---->
    <div v-if="tjShow" id="submit" @click="submit">
      
    </div>
    <div v-else id="submit" class="gary">
      
    </div>

    <!---->
    <div id="footer">
      <ul>
        <router-link to="/love/makeFriend" style="text-align: center;">
          <li>
            <img src="./images/jy-7.png" alt="">
            <span></span>
          </li>
        </router-link>
        <router-link to="/love/dynamics" class="red" style="text-align: center;">
          <li>
            <img src="./images/jy-10.png" alt="">
            <span></span>
          </li>
        </router-link>
        <router-link to="/love/lPerCenter" style="text-align: center;">
          <li>
            <img style="margin-right: 0.1rem;" src="./images/jy-11.png" alt="">
            <span></span>
          </li>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        imgList: [],
        imgMore: [],
        mine: '',
        userid: '',
        show: false,
        bigImage: '',
        id: '0',
        type: "",  //  ios   1 2ios
        tjShow: true
      }
    },
    created: function () {
      clearTimeout(this.setTime);
      $("#title").text("");
      this.$MintUI.Indicator.close();
      this.userid = this.WX_UserId;
      this.$nextTick(function () {
        $(".weui-picker-container").hide();
      });
    },
    mounted: function () {
      this.getSignature();//
      if (this.$route.query.id != undefined) {
        this.id = this.$route.query.id;
        this.getMsg();
      }
    },
    methods: {
      getSignature: function () {   //
        var that = this;
        var u = navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        if (isAndroid) {
          that.type = "1";
        } else if (isiOS) {
          that.type = "2";
        }
        var url = window.location.href;
        if (window.location.hash.length) {
          url = url.substr(0, url.indexOf(window.location.hash));
        }
        $.ajax({
          type: "post",
          url: that.port + "api/Service/GetJsApiConfig",
          data: {url: url},
          dataType: "json",
          success: function (res) {
            var result = JSON.parse(res.data);
            wx.config({
              debug: false, // ,apialertpclogpc
              appId: that.Crop_Id + '', // cropID
              timestamp: result.timestamp, // 
              nonceStr: result.nonceStr + '', // 
              signature: result.signature + '',// 1
              jsApiList: ['chooseImage', 'uploadImage', 'getLocalImgData', 'previewImage'] // JSJS2
            });
          }, error: function () {
            that.wxError();
          }
        });
      },
      iosUpImg: function (e) {   //ios,,
        var that = this;
        wx.getLocalImgData({
          localId: e, // localID
          success: function (res) {
            var localData = res.localData; // localDatabase64img
            var fileName = "1.jpg";
            $.ajax({
              url: that.port + "api/FeedBack/UpImage",
              type: "POST",
              data: {
                base64_string: localData,
                fileExt: fileName
              },
              dataType: "json",
              success: function (res) {
                if (res.returncode == 0) {
                  that.imgMore.push(res.data);
                  that.imgList.push(that.port + res.data);
                } else {
                  that.$MintUI.Toast("");
                }
              }, error: function () {
                that.$MintUI.Toast("");
              }
            });
          }
        });
      },
      androidUpImg: function (e) {   //  ,,,
        var that = this;
        wx.uploadImage({
          localId: e, // IDchooseImage
          isShowProgressTips: 1, // 1
          success: function (res) {
            var media_id = res.serverId; // ID
            //  
            $.ajax({
              url: that.port + "api/Service/GetMedia",
              data: {
                media_id: media_id
              },
              type: "post",
              dataType: "json",
              success: function (res) {
                if (res.returncode == 0) {
                  that.imgMore.push(res.data);
                  that.imgList.push(that.port + res.data);
                } else {
                  that.$MintUI.Toast("");
                }
              },
              error: function (XMLHttpRequest, textStatus, errorThrown) {
                that.$MintUI.Toast("");
              }
            });
          }
        });
      },
      //
      xmTanUploadImg: function (e) {
        var that = this;
        wx.ready(function () {
          wx.chooseImage({
            count: 9, // 9
            sizeType: ['original', 'compressed'], // 
            sourceType: ['album', 'camera'], // 
            defaultCameraMode: "batch", //normalbatchnormalbatchnormal
            success: function (res) {
              if (that.type == "1") {
                for (var i = 0; i < res.localIds.length; i++) {
                  var localIds = res.localIds[i].toString();
                  that.androidUpImg(localIds);
                }
              } else {
                for (var i = 0; i < res.localIds.length; i++) {
                  var localIds1 = res.localIds[i].toString();
                  that.iosUpImg(localIds1);
                }
              }
              // andriodlocalIdimgsrc
              // IOSgetLocalImgDatabase64img

            }
          });
        });
      },
      getMsg: function () {
        var that = this;
        // that.$MintUI.Indicator.open("");
        this.setTime = setTimeout(function () {
          that.$MintUI.Indicator.open("");
        }, that.timeNum);
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/DynamicDetail",
          data: {userid: that.userid, id: that.id},
          dataType: "json",
          success: function (res) {
            clearTimeout(that.setTime);
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              console.log(data);
              debugger
              that.mine = decodeURI(data.dynamic.content);
              if(data.img!=null){
                that.imgMore = data.img;
              }
              if (data.img != '' && data.img != null) {
                for (var i = 0; i < data.img.length; i++) {
                  that.imgList.push(that.port + data.img[i]);
                }
              }

              that.$MintUI.Indicator.close();
            } else {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast(res.msg);
            }

          }, error: function () {
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            return false;
          }
        });
      },
      imgSize: function (e, list) {  //
        // 				this.bigImage=e;
        // 					this.show=true;
        wx.previewImage({
          current: e, // http
          urls: list // http
        });
      },
      hideImg: function () {
        this.bigImage = "";
        this.show = false;
      },
      del: function (e) {  //
        this.imgList.splice(e, 1);
        this.imgMore.splice(e, 1);
      },
      submit: function () {   //
        var that = this;


        if (this.mine == "" || this.mine == null) {
          that.$MintUI.Indicator.close();
          that.$MintUI.Toast("");
          return false;
        }
        /* else if (that.imgMore.length == 0) {
           that.$MintUI.Indicator.close();
           that.$MintUI.Toast("");
           return false;
         }*/ else if (that.imgMore.length > 9) {
          that.$MintUI.Toast("");
          return false;
        }
        var imgMore = that.imgMore.join(',');
        that.$MintUI.Indicator.open("");
        that.tjShow = false;
        // alert(that.mine);
        var mine = encodeURI(that.mine);
        // alert(that.mine);
        // console.log(that.mine);
        // console.log(mine);
        // alert(mine);
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/AddQueQiaoDynamic",
          data: {
            id: that.id,
            userid: that.userid,
            content: mine,
            img: imgMore
          },
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast({
                message: '',
                position: 'center',
                duration: 1400
              });
              setTimeout(function () {
                that.$router.push('/love/perDt');
              }, 1500);

            } else {
              that.$MintUI.Toast("");
              that.tjShow = true;
              return false;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
            that.tjShow = true;
            return false;
          }
        });

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "./css/dynamics.css";

  #dynamics {
    width: 100%;
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  #submit.gary {
    background-color: #ccc;
  }

  /*# sourceMappingURL=home.css.map */
</style>



