<template>
  <div class="msgActivity">
    <div style="position: fixed;width: 100%;height: 40px;background: red;bottom: 0;left: 0;"></div>
    <!---->
    <div id="search">
      <input type="text" placeholder="" v-model="name"><img @click="btnSearch" src="./images/jy-15.png" alt="">
    </div>
    <div id="choose">
      <div>
        {{totalMsg}}
      </div>
      <div @click="show= !show">
        <span></span>
        <img style="width:.6rem;margin-left: .2rem;" src="./images/jy-16.png" alt="">
      </div>
    </div>
    <!---->
    <div id="toScroll" style="width: 100%;height: 100%;overflow: auto;-webkit-overflow-scrolling: touch;position: relative;
" v-infinite-scroll="load" infinite-scroll-disabled="loading"
         infinite-scroll-distance="1">
      <div id="List">
        <div class="list" v-for="item in list" @click="detail(item.userid)">
          <div class="headImg">
            <div v-if="item.isfollow == 1&&item.userid!=userid" class="isfollow">
              <img src="./images/jy-21+.png">
            </div>
            <!--<img v-if="item.img!=null" :src="item.img" alt="">-->
            <img v-changeSize v-if="item.img!=null" :src="item.img" alt="">
            <img v-else src="./images/normal.png" style="width: 100%;height: 100%;">
          </div>
          <div class="personalInfo">
            <div class="personalInfoLeft">
              <span>{{item.name}}</span>
              <img v-if="item.gender==1" src="./images/jy-5.png">
              <img v-if="item.gender==2" src="./images/jy-4.png">
            </div>
            <div class="personalInfoRight">
              {{item.age}}/{{item.departName}}
              <!--<span class="marry" v-if="item.status==0"></span><span class="marry" v-if="item.status==1"></span>-->
            </div>
            <!-- <p class="declaration" v-if="item.remark!=null"><span><img src="./images/declaration.png" alt=""><span>{{item.remark}}</span></span>
             </p>-->
            <!---->
            <!--<p class="dt" v-if="item.dongtai!=''">{{item.dongtai}}</p>-->
          </div>
          <div class="description" v-html="item.remark !=null && item.remark !=''?decodeURI(item.remark):'TA...'">
            {{decodeURI(item.remark)}}
          </div>
        </div>
        <!---->
        <div class="toLoadingShow" v-show="loadingShow"
             style="overflow:hidden;margin:0 auto;color:#aaa;display: flex;align-items: center;justify-content: center;">
          <mt-spinner style="display: inline-block;" :type="3" color="#aaa" :size="20"></mt-spinner>
          <span style="font-size: 12px;"></span>
        </div>
        <p class="toLoadingShow" v-show="noMoreData"
           style="margin-top: .5rem;color: #aaa;text-align: center;clear: both;font-size: 12px;">
          ------------
        </p>
        <p class="toLoadingShow" v-show="noData"
           style="padding-top: .5rem;color: #aaa;text-align: center;clear: both;font-size: 12px;">
          ------------</p>
        <!-- <div id="more" @click="more" v-show="list.length>=10 && ishow">
           
         </div>-->
        <!---->
        <!-- <div id="more" style="text-align: center;margin-top: 0.5rem;" v-show="list.length>=10 && !ishow">
           <p>~</p>
         </div>-->
        <!--<p style="color: #aaa;text-align: center;padding: .5rem">&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;&#45;</p>-->
        <!---->
        <!--<div style="text-align: center;margin-top: 0.5rem;" class="no_msg" v-show="list.length==0">
          <img style="width:80%;" src="./images/nodata.png" alt="">
          <p></p>
        </div>-->
      </div>
    </div>
    <!--<router-link to="/love/lPlain" id="plain">
      <img src="./images/plain.png" alt="">
      <span style="display: block;"></span>
    </router-link>-->
    <!---->
    <div id="footer">
      <ul>
        <router-link to="/love/makeFriend" class="red" style="text-align: center;">
          <li>
            <img src="./images/jy-8.png" alt="">
            <span></span>
          </li>
        </router-link>
        <router-link to="/love/dynamics" style="text-align: center;">
          <li>
            <img src="./images/jy-9.png" alt="">
            <span></span>
          </li>
        </router-link>
        <router-link to="/love/lPerCenter" style="text-align: center;">
          <li>
            <img style="margin-right: 0.1rem;" src="./images/jy-11.png" alt="">
            <span></span>
          </li>
        </router-link>
      </ul>
    </div>
    <!---->
    <div id="model" v-show="show" @click="model"></div>
    <div id="sort" v-show="show">
      <ul>
        <li @click="setSort(0)" :class="{'red':sort==0}" id="0"><span></span><span class="yes"
                                                                                       v-show="sort==0"></span>
        </li>
        <li :class="{'red':sort==1}" id="1">
          <span></span>
          <label style="margin-left:2.3rem;color:#333;">
            <input @change="radio(1)" type="radio" class="sex" name="sex">
          </label>
          <label style="margin-left:1.79rem;color:#333;">
            <input @change="radio(2)" type="radio" class="sex" name="sex">
          </label>
          <span class="yes" v-show="sort==1"></span>
        </li>
        <li :class="{'red':sort==2}" id="2">
          <span></span>
          <input style="width:7rem;height:1.5rem;border: none;outline: none;margin-left: 1.5rem;"
                 @click="getJsApiConfig" v-model="showDept" readonly="" unselectable="on" onfocus="this.blur()"
                 type="text"
                 placeholder="">
          <!-- <select v-model="dept" id="department" @change="sortDept">
          <option value=""></option>
          <option v-for="item in deptSort" :value="item.name">{{item.name}}</option>
      </select> -->
          <span class="yes" v-show="sort==2"></span>
        </li>
        <!-- <li :class="{'red':sort==3}" id="3">
        <span></span>
        <label style="margin-left:1.5rem;color:#333;">
            <input @change="marryS(1)" type="radio" class="marry" name="marry">
        </label>
        <label style="margin-left:1rem;color:#333;">
            <input @change="marryS(0)" type="radio" class="marry" name="marry">
        </label>
        <span class="yes" v-show="sort==3"></span>
    </li> -->
        <li :class="{'red':sort==4}" id="4">
          <span></span>
          <select name="" id="age" v-model="age" @change="sortAge">
            <option value="0"></option>
            <option value="18-22">18-22</option>
            <option value="23-25">23-25</option>
            <option value="26-29">26-29</option>
            <option value="30-35">30-35</option>
            <option value="36-40">36-40</option>
            <option value="40-70">40</option>
          </select>
          <span class="yes" v-show="sort==4"></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'msgActivity',
    data() {
      return {
        show: false,
        sort: '0',
        sex: '',
        status: '0',
        list: [],
        pageIndex: 0,         //
        pageSize: 10,         //
        sortName: 0,
        age: '0',  //
        ageMin: '0',  //
        ageMax: '0',  //
        sortSex: '0',  //
        dept: '',  //
        name: '',   //
        deptSort: [],  //
        ishow: true,
        loadingShow: false,
        loading: true,
        totalMsg: 0, //
        noData: false,
        noMoreData: false,
        showDept: '',
        oParent: '',//
        aChild: '', //
        aHeight: [], //
        oneWidth: ''
      }
    },
    created: function () {
      $("#title").text("");
      this.$MintUI.Indicator.close();
      this.userid = this.WX_UserId;
      this.url1 = this.port;
      clearTimeout(this.setTime);
      this.$nextTick(function () {
        $(".weui-picker-container").hide();
      });
      // this.$MintUI.Indicator.open('');
    },
    mounted: function () {
      this.load();
      //this.department();
      this.getSignature();
      // document.getElementById('List').addEventListener('scroll', this.toScroll)
    },
    //
    directives: {
      //
      changeSize: {
        inserted: function (el) {
          let getImgInfo = function (url) {
            return new Promise(resovle => {
              let img = new Image();
              let json = {};
              img.src = url;
              img.onload = function () {
                json['width'] = img.width;
                json['height'] = img.height;
                resovle(json);
              }
            })
          };
          let maxwidth = el.parentNode.offsetWidth;
          getImgInfo(el.src).then(res => {
            var that = this;
            var theWidth = el.width;
            var theHeight = el.height;
            if (theWidth > theHeight) {
              el.style.height = '100%';
              el.style.width = 'auto';
            } else {
              el.style.height = 'auto';
              el.style.width = '100%';
            }
          })
        },
      }
    },
    methods: {
      //
      waterFall: function (parent, children) {
        var oParent = document.getElementById(parent);
        var aChild = oParent.getElementsByClassName(children);
        var oneWidth = aChild[0].offsetWidth;
        // this.cols = Math.floor((document.documentElement.clientWidth || document.body.clientWidth) / this.oneWidth);  // /=Math.floor()
        var cols = 2;
        var aHeight = []; //
        for (var i = 0; i < aChild.length; i++) {
          if (i < cols) { //i < cols
            aChild[i].style.top = 0; // top0
            aChild[i].style.left = i * oneWidth + 'px'; // left=*
            aHeight[i] = aChild[i].offsetHeight; // aHeight
          } else {
            var minHeight = Math.min.apply(null, aHeight), // Math.min.apply()
              minIdx = aHeight.indexOf(minHeight); // 
            aChild[i].style.top = minHeight + 'px'; // top
            aChild[i].style.left = minIdx * oneWidth + 'px'; // left*
            aHeight[minIdx] += aChild[i].offsetHeight; //
          }
        }
        var maxHeight = Math.max.apply(null, aHeight);
        // $('.list:last')

        oParent.style.height = maxHeight + 'px'; //absolute

        // $('.toLoadingShow').css('bottom', -40)


        // oParent.style.width = cols * oneWidth + 'px'; //
        /* setTimeout(function () {
           if(this.pageIndex <= 1){
             $('.toLoadingShow').css('top', 0);
           }else{
             var toLoadingShowH = 30;
             var theLast =document.getElementById('List').offsetHeight;
             $('.toLoadingShow').css('bottom', '-40px');
           }

         }, 500)*/
      },

      //,
      getSignature: function () {
        var that = this;
        var url = window.location.href;
        if (window.location.hash.length) {
          url = url.substr(0, url.indexOf(window.location.hash));
        }
        $.ajax({
          type: "post",
          url: that.port + "api/Service/GetJsApiConfig",
          data: {url: url},
          dataType: "json",
          success: function (res) {
            var result = JSON.parse(res.data);
            wx.config({
              beta: true,// wx.invokejsapi
              debug: false, // ,apialertpclogpc
              appId: that.Crop_Id + '', // cropID
              timestamp: result.timestamp, // 
              nonceStr: result.nonceStr + '', // 
              signature: result.signature + '',// 1
              jsApiList: ['selectEnterpriseContact'] // JSJS2
            });
          }, error: function () {
            that.wxError();
          }
        });
      },
      getJsApiConfig: function () {
        var that = this;
        wx.ready(function () {
          that.modelShow();
        });

      },
      modelShow: function () {  //,
        var that = this;
        wx.invoke("selectEnterpriseContact", {
            "fromDepartmentId": 0, // -1, 0
            "mode": "single", // singlemulti
            "type": ["department"]  // departmentuser
          }, function (res) {
            that.dept = "";
            that.list = [];
            that.pageindex = 0;
            if (res.err_msg == "selectEnterpriseContact:ok") {
              if (typeof res.result == 'string') {
                res.result = JSON.parse(res.result) //result
              }
              var selectedDepartmentList = res.result.departmentList;// 
              if (selectedDepartmentList.length != 0) {
                for (var i = 0; i < selectedDepartmentList.length; i++) {
                  that.showDept = selectedDepartmentList[i].name;
                  that.dept = selectedDepartmentList[i].id;
                  // that.sort = 2;
                  // that.ageMin = "0";
                  // that.ageMax = "0";
                  // that.sortSex = "0";
                  // that.age = "0";
                  // that.status = "0";
                }
              }
            }
          }
        )
      },
      //
      load: function () {
        var that = this;
        that.loading = true;
        if (that.pageIndex > 0) {
          that.loadingShow = true;
        }
        /*this.setTime = setTimeout(function () {
          that.$MintUI.Indicator.open("");
        }, that.timeNum)*/
        // that.pageindex = 0;
        $.ajax({
          type: "post",
          url: that.port + "api/QueQiao/GetQueQiaoList",
          data: {
            userid: that.userid,
            name: that.name,
            ageMin: that.ageMin,
            ageMax: that.ageMax,
            sortSex: that.sortSex,
            Dept: that.dept,
            status: that.status,
            pageIndex: that.pageIndex,
            pageSize: that.pageSize
          },
          dataType: "json",
          success: function (res) {
            that.loadingShow = false;
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              that.$MintUI.Indicator.close();
              // that.list = [];
              var data = JSON.parse(res.data).queqiaolist;
              that.totalMsg = JSON.parse(res.data).count;
              for (var i = 0; i < data.length; i++) {
                if (data[i].img == null) {
                  // data[i].img = "./images/mrHeader.jpg";
                } else {
                  data[i].img = that.port + data[i].img;
                }
              }
              for (var i = 0; i < data.length; i++) {
                that.list.push(data[i])
              }
              console.log(that.list);
              that.$nextTick(function () {
                that.waterFall('List', 'list');
              });

              if (data.length < that.pageSize) {
                // $('.list:last .headImg').css('height','10rem');
                that.loading = true;
                if (that.pageIndex == 0 && that.list.length <= 0) {
                  that.noData = true
                } else {
                  that.noMoreData = true;
                }
                return false;
              } else {
                that.loadingShow = false;
                clearTimeout(that.setTime);
                that.noData = false;
                that.noMoreData = false;
                that.$nextTick(function () {
                  that.loading = false;
                  that.pageIndex++;
                })
              }
              that.$MintUI.Indicator.close();
            } else {
              that.$MintUI.Indicator.close();
              that.$MintUI.Toast(res.msg);
            }
          }, error: function () {
            that.loadingShow = false;
            clearTimeout(that.setTime);
            that.$MintUI.Indicator.close();
            that.$MintUI.Toast("");
          }
        });
      },
      btnSearch: function () {
        this.$MintUI.Indicator.open("");
        this.load();
      },
      Ishow: function () {    //
        this.show = true;
      },
      setSort: function (e) {   //
        this.sort = e;
        var sex = document.querySelectorAll(".sex");
        for (var i = 0; i < sex.length; i++) {
          sex[i].checked = false;
        }
        this.sex = "";
        var marry = document.querySelectorAll(".marry");
        for (var i = 0; i < marry.length; i++) {
          marry[i].checked = false;
        }
        this.marry = "";
        this.dept = "";
        this.ageMin = "0";
        this.ageMax = "0";
        this.sortSex = "0";
        this.age = "0";
        this.status = "0";
      },
      radio: function (e) {  //
        // this.sort = 1;
        // this.sex = e;
        var marry = document.querySelectorAll(".marry");
        for (var i = 0; i < marry.length; i++) {
          marry[i].checked = false;
        }
        // this.dept = "";
        // this.ageMin = "0";
        // this.ageMax = "0";
        this.sortSex = e;
        // this.age = "0";
        // this.status = "0";
      },
      /* sortDept: function () {    //
         this.sort = 2;
         this.ageMin = "0";
         this.ageMax = "0";
         this.sortSex = "0";
         this.age = "0";
         this.status = "0";
       },*/
      /* marryS: function (e) {   //
         this.sort = 3;
         this.status = e;
         var sex = document.querySelectorAll(".sex");
         for (var i = 0; i < sex.length; i++) {
           sex[i].checked = false;
         }
         this.sex = "";
         this.dept = "";
         this.ageMin = "0";
         this.ageMax = "0";
         this.sortSex = "0";
         this.age = "0";
       },*/
      sortAge: function () {   //
        // this.sort = 4;
        var that = this;
        this.ageMin = that.age.split("-")[0];
        this.ageMax = that.age.split("-")[1];
        // this.sortSex = "0";
        // this.status = "0";
        // this.dept = "";
      },
      model: function () {
        var that = this;
        /* this.setTime = setTimeout(function () {
           debugger
           that.$MintUI.Indicator.open("");
         },that.timeNum)*/
        that.show = false;
        /*if (that.sort == 0) {
          that.sortName = ""
        } else if (that.sort == 1) {
          that.sortName = ""
        } else if (that.sort == 2) {
          that.sortName = ""
        } else if (that.sort == 3) {
          that.sortName = ""
        } else if (that.sort == 4) {
          that.sortName = ""
        }*/
        this.list = [];
        this.noMoreData = false;
        this.noData = false;
        this.pageIndex = 0;
        that.load();
      },
      detail: function (e) {  //
        this.$router.push({
          path: '/love/detail',
          query: {
            id: e
          }
        })
      },
      department: function () {   //,
        var that = this;
        $.ajax({
          type: "post",
          url: that.port + "api/UserBaseInfo/GetDeptList",
          dataType: "json",
          success: function (res) {
            that.$MintUI.Indicator.close();
            if (res.returncode == 0) {
              var data = JSON.parse(res.data);
              that.deptSort = data;
            }

          }, error: function () {
            that.$MintUI.Indicator.close();
          }
        });
      }
    },
    watch: {
      name: function (value) {
        this.name = value;
        this.list = [];
        this.noMoreData = false;
        this.noData = false;
        this.pageIndex = 0;
        clearTimeout(this.setTime)
        // $('#List').height(50);
        // $('.toLoadingShow').css('bottom', 0)
        this.load();
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "./css/msgActivity.css";

  #plain {
    width: 3rem;
    height: 3rem;
    position: fixed;
    bottom: 3.5rem;
    right: 0.5rem;
    background-color: #fff;
    border-radius: 50%;
    text-align: center;
    color: #666;
    font-size: 0.5rem;
    box-shadow: 0px 0px 16px 0px rgba(200, 200, 200, 0.75);
  }

  #plain img {
    width: 1rem;
    height: 0.75rem;
    margin-top: 0.7rem;
  }

  .toLoadingShow {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    height: 2rem;
    text-align: center;
    bottom: -40px;
    left: 0;
  }


  /*# sourceMappingURL=home.css.map */
</style>



