<template>
	<div class="lPlain">
		<div id="one">
			<h5></h5>
			<p style="text-indent: 2em;"></p>
		</div>
		<div id="two">
			<h5></h5>
			<p style="font-size: 0.7rem;color:#333;">1</p>
			<p>
				&nbsp;&nbsp;&nbsp;&nbsp;->>
			</p>
			<div>
				<img v-for="item in oneImg" @click="imgShow(item)" :src="item" alt="">
			</div>
			<p class="sm">
				<img src="./images/one04.jpg" alt=""><img src="./images/one05.jpg" alt=""></p>
		</div>
		<div id="two" style="padding-top: 0;">
			<p style="font-size: 0.7rem;color:#333;">2</p>
			<p>
				&nbsp;&nbsp;&nbsp;&nbsp;->->
			</p>
			<div>
				<img v-for="item in twoImg" @click="imgShow(item)" :src="item" alt="">
			</div>
			<p class="sm"><img style="width:1rem;" src="./images/two04.jpg" alt=""><img style="width:1rem;" src="./images/two03.jpg" alt=""><span style="color:red;">+</span><span style="color:red;"></span></p>
		</div>
		<div id="two" style="padding-top: 0;">
			<p style="font-size: 0.7rem;color:#333;">3</p>
			<p>
				&nbsp;&nbsp;&nbsp;&nbsp;->->
			</p>
			<div>
				<img v-for="item in threeImg" @click="imgShow(item)" :src="item" alt="">
			</div>
			<p><img style="width:1.5rem;" src="./images/three04.jpg" alt=""><img style="width:1.5rem;" src="./images/three05.jpg"
				 alt=""><img style="width:2rem;" src="./images/three06.jpg" alt=""></p>
		</div>

		<div @click="hide" id="model" style="position: fixed;left:0;top:0;width:100%;height:100%;" v-show="show">
			<img style="width:100%;height:100%;" :src="img" alt="">
		</div>
	</div>


</template>

<script>
	export default {
		name: 'HelloWorld',
		data() {
			return {
				oneImg: [
					require("./images/one01.jpg"),
					require("./images/one02.jpg"),
					require("./images/one03.jpg")
				],
				twoImg: [
					require("./images/two01.jpg"),
					require("./images/two02.jpg")
				],
				threeImg: [
					require("./images/three01.jpg"),
					require("./images/three02.jpg"),
					require("./images/three03.jpg")
				],
				img: "",
				show: false
			}

		},
		created: function () {
			$("#title").text("");
			this.$MintUI.Indicator.close();
			this.userid = this.WX_UserId;
      this.$nextTick(function () {
        $(".weui-picker-container").hide();
      });
		},
		mounted: function () {
		},
		methods: {
			imgShow: function (e) {
				console.log(e);
				this.img = e;
				this.show = true;
			},
			hide: function () {
				this.img = "";
				this.show = false;
			},
			imgSize: function (e, list) {  //
				/* this.bigImage=this.url+e;
					this.imgShow=true; */
				var that = this;
				var arr = [];
				for (var i = 0; i < list.length; i++) {
					arr.push(that.url + list[i]);
				}
				wx.ready(function () {
					wx.previewImage({
						current: e, // http
						urls: arr // http
					});
				});
			}

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	* {
		padding: 0;
		margin: 0;
		list-style: none;
		font-family: PingFang-SC-Medium !important;
		word-break: break-all;
	}


	body {
		background-color: #f5f5f5;
		width: 100%;
	}

	#one,
	#two {
		padding: 0.5rem 0.5rem;
		background-color: #fff;
		margin-bottom: 0.5rem;
	}

	#one h5,
	#two h5 {
		font-size: 0.8rem;
		color: #333;
		margin-bottom: 0.3rem;
	}

	#two {
		margin-bottom: 0;
	}

	#one p,
	#two p {
		font-size: 0.7rem;
		color: #888;
		padding: 0.3rem 0;
	}

	#two>div {
		overflow: hidden;
		margin: 0.5rem 0;
	}

	#two>div img {
		width: 31%;
		margin: 0 1.15%;
		float: left;
	}

	#two>.sm {
		padding-bottom: 1rem;
		border-bottom: 1px solid #efefef;
	}

	#two>p img {
		width: 1rem;
		vertical-align: middle;
	}

  .lPlain{
    width: 100%;
    height: 100%;
    overflow: auto;
  }

	/*# sourceMappingURL=home.css.map */
</style>



